import Exercice from '../Exercice.js'
import { context } from '../../modules/context.js'
import { listeQuestionsToContenu, randint, choice, ecritureNombreRelatif, ecritureNombreRelatifc, ecritureAlgebrique, ecritureParentheseSiNegatif } from '../../modules/outils.js'
import { ajouteChampTexte, setReponse } from '../../modules/gestionInteractif.js'

export const titre = 'Soustraction de deux entiers relatifs'
export const interactifReady = true
export const amcReady = true
export const amcType = 4
/**
* Effectuer la soustraction de  2 nombres relatifs.
*
* * On peut param√©trer la distance √† z√©ro maximale des deux termes (par d√©faut √©gale √† 20)
* * On peut choisir d'avoir une √©criture simplifi√©e  (par d√©faut ce n'est pas le cas)
* @Auteur R√©mi Angot
* 5R21
*/
export default function Exercice_soustractions_relatifs (max = 20) {
  Exercice.call(this) // H√©ritage de la classe Exercice()
  this.sup = max
  this.sup2 = false // √©criture simplifi√©e
  this.titre = titre
  this.consigne = 'Calculer'
  this.spacing = 2
  this.interactif = true
  this.interactifReady = interactifReady
  this.amcType = amcType
  this.amcReady = amcReady

  this.nouvelleVersion = function () {
    this.listeQuestions = [] // Liste de questions
    this.listeCorrections = [] // Liste de questions corrig√©es
    for (let i = 0, a, b, k, texte, texteCorr, cpt = 0; i < this.nbQuestions && cpt < 50;) { // On limite le nombre d'essais pour chercher des valeurs nouvelles
      a = randint(1, this.sup)
      b = randint(1, this.sup)
      k = choice([[-1, -1], [-1, 1], [1, -1]]) // Les deux nombres relatifs ne peuvent pas √™tre tous les deux positifs
      a = a * k[0]
      b = b * k[1]
      if (this.sup2) {
        texte = `$ ${a} - ${ecritureParentheseSiNegatif(b)} = \\dotfill $`
		if (this.interactif && !context.isAmc) {
			texte = ajouteChampTexte(this, i, { texte: `$ ${a} - ${ecritureParentheseSiNegatif(b)} = $` })
		  }
        if (b > 0) {
          texteCorr = `$ ${a} - ${ecritureParentheseSiNegatif(b)} = ${a - b} $`
        } else {
          texteCorr = `$ ${a} - ${ecritureParentheseSiNegatif(b)} = ${a} ${ecritureAlgebrique(-b)} = ${a - b}$`
        }
      } else {
        texte = '$ ' + ecritureNombreRelatif(a) + ' - ' + ecritureNombreRelatif(b) + ' = \\dotfill $'
		if (this.interactif && !context.isAmc) {
			texte = ajouteChampTexte(this, i, { texte: `$ ${a} - ${ecritureParentheseSiNegatif(b)} = $` })
		  }
        texteCorr = '$ ' + ecritureNombreRelatif(a) + ' - ' + ecritureNombreRelatif(b) + ' = ' + ecritureNombreRelatifc(a) + ' + ' + ecritureNombreRelatifc(-1 * b) + ' = ' + ecritureNombreRelatifc(a - b) + ' $'
      }
      if (this.listeQuestions.indexOf(texte) === -1) { // Si la question n'a jamais √©t√© pos√©e, on en cr√©√© une autre
        this.listeQuestions.push(texte)
        this.listeCorrections.push(texteCorr)
		setReponse(this, i, a - b, { signe: true })
        i++
      }
      cpt++
    }
    listeQuestionsToContenu(this)
  }
  this.besoinFormulaireNumerique = ['Valeur maximale', 99999]
  this.besoinFormulaire2CaseACocher = ['Avec des √©critures simplifi√©es']
}
